@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,500&display=swap');

:root {
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 16px;
  font-weight: bold;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html,
body,
#root,
.app {
  height: 100%;
  background-color: #333;
  color: white;
}

.app {
  padding: 1rem;
}
.app h1 {
  text-align: center;
  margin-bottom: 1rem;
}

/* BUTTONS */
button {
  display: inline-block;

  width: fit-content;
  padding: 10px;
  border: 2px solid #777;
  border-radius: 4px;

  font-family: inherit;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background-color: #777;
}
button:hover {
  cursor: pointer;
}
button:active {
  cursor: pointer;
  filter: brightness(75%);
}
/* END BUTTONS */

/* CARDS */
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 128px);
  grid-auto-rows: 128px;
  gap: 1rem;
  justify-content: center;
  align-content: center;

  width: 100%;
  padding: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;

  width: 100%;
  border-radius: 0.25rem;
  border: 8px solid #777;

  text-align: center;
  background-color: #777;
  color: #fff;
}
.card:hover {
  cursor: pointer;
  border: 8px solid hsl(209, 100%, 50%);
}
.card > * {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.card img {
  height: 64px;
  width: 64px;
}
/* END CARDS */

/* MODAL */
.modal-click-bg {
  position: absolute;
  top: 0;
  left: 0;

  min-height: 100vh;
  height: 100%;
  width: 100%;

  background-color: rgba(0, 0, 0, 0.5);
}
.modal-click-bg:hover {
  cursor: pointer;
}

.modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  border-radius: 4px;

  overflow: hidden;
  color: #333;
  background-color: #ccc;
}
/* END MODAL */

/* INFO HEADER */
.info-header {
  display: flex;
  gap: 1rem;

  margin-bottom: 1rem;
}
.info-header > :first-child {
  flex: 1;
  width: 100%;
}
.score-info {
  display: grid;
  grid-template-columns: 1fr 16px;
  grid-auto-rows: fit-content;
  column-gap: 1rem;

  height: fit-content;

  text-align: left;
}
/* END INFO HEADER */

.invert {
  filter: brightness(0) invert(1);
}

.hidden {
  display: none;
}

@media (max-width: 600px) {
  :root {
    font-size: 14px;
  }

  .info-header {
    flex-direction: column;
  }

  .card-grid {
    grid-template-columns: repeat(auto-fit, 64px);
    grid-auto-rows: 64px;
    gap: 0.25rem;

    padding: 0.25rem;
  }

  .card h5 {
    display: none;
  }
}
